.main-footer {
  &__form {
    margin: 0 auto 40px;
    padding: 40px;
    max-width: 760px;
    color: white;
    background: $fun-blue;
  }
}
.form {
  &__title {
    margin-bottom: 20px;
    @include worksansbold;
    font-size: 48px;
    text-align: center;
    @media screen and (max-width: 576px) {
      font-size: 32px;
    }
  }
  &-group {
    margin-bottom: 14px;
  }
  .form-group-double {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @media screen and (max-width: 576px) {
      display: block;
    }
    input {
      flex: 1;
      &:first-child {
        margin-right: 14px;
        @media screen and (max-width: 576px) {
          margin-right: 0;
          margin-bottom: 14px;
        }
      }
    }
  }
  input[type="text"],
  input[type="number"],
  input[type="email"],
  textarea {
    padding: 12px;
    width: 100%;
    font-size: 21px;
    color: white;
    background: transparent;
    border: 1px solid rgba(255, 255, 255, 0.6);
  }
  input[type="text"],
  input[type="email"] {
    @include placeholder-input($anakiwa);
  }
  textarea {
    height: 140px;
    @include placeholder-textarea($anakiwa);
  }
  input[type="submit"] {
    display: table;
    margin: 30px auto 0;
  }
}
.privacy {
  font-size: 16px;
  color: $anakiwa;
  a {
    font-size: 16px;
    color: $dodger-blue;
    &:hover {
      text-decoration: none;
      color: white;
    }
  }
}
input[type="submit"] {
  padding: 14px 30px;
  @include worksansbold;
  font-size: 22px;
  text-transform: capitalize;
  border: none;
  color: #fff;
  background-color: $dodger-blue;
  transition: all 0.7s ease-in-out;
  &:hover {
    background-color: #fff;
    color: $fun-blue;
  }
}
.main-footer__socials {
  display: table;
  margin: 0 auto 60px;
  text-align: center;
  &-item {
    margin-bottom: 10px;
    a {
      margin-left: 10px;
    }
    &.main-footer__socials-item--center {
      display: flex;
      align-items: center;
      @media screen and (max-width: 576px) {
        display: block;
      }
      span {
        @media screen and (max-width: 576px) {
          display: block;
          margin-bottom: 10px;
        }
      }
    }
  }
  a {
    color: $fun-blue;
  }
}

.main-footer__bottom {
  padding: 60px 0;
  color: white;
  background: $fun-blue;
  &-wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @media screen and (max-width: 992px) {
      display: block;
    }
  }
}
.main-footer__languages {
  display: flex;
  align-items: center;
  @media screen and (max-width: 992px) {
    margin-bottom: 60px;
  }
  a {
    display: block;
    margin-right: 84px;
    color: white;
    @media screen and (max-width: 1300px) {
      margin-right: 40px;
    }
    @media screen and (max-width: 400px) {
      margin-right: 20px;
    }
  }
}
.main-footer__menu {
  column-count: 3;
  column-gap: 180px;
  @media screen and (max-width: 1300px) {
  	column-gap: 80px;
  }
  @media screen and (max-width: 576px) {
    column-count: initial;
  }
  a {
    display: block;
    margin-bottom: 10px;
    color: $anakiwa;
  }
}
