html,
body {
  font-size: 17px;
}

body {
  margin: 0;
  padding: 0;
  //@include franklinkgothicdemi;
  //@include worksansbold;
  //@include ptsansnarrowbold;
  //@include ptsansnarrowbold;
  @include frankbook;
  font-size: 20px;
  line-height: 1.3;
  color: $fun-blue;
  background: white;
}

* {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

ul {
  @include reset-list;
}

p {
  margin: 0;
  padding: 0;
}
a {
  text-decoration: none;
}

.hidden {
  display: none;
}

.container {
  margin: 0 auto;
  max-width: 1790px;
  padding: 0 15px;
}
.btn {
  position: relative;
  display: inline-block;
  padding: 20px;
  @include franklinkgothicdemi;
  font-size: 24px;
  transition: all 0.3s ease-in-out;
  background: $fun-blue;
  color: white;
  border: none;
  &:hover {
    padding: 20px 70px 20px 20px;
    color: $fun-blue;
    background: white;
    &::after {
      opacity: 1;
    }
  }
  &::after {
    content: '';
    position: absolute;
    top: 47%;
    right: 10px;
    width: 30px;
    height: 9px;
    background: url("../i/arrow.png");
    background-size: cover;
    transition: all .5s;
    opacity: 0;
  }
}
.section {
  &__title {
    margin-bottom: 50px;
    @include worksansbold;
    font-size: 60px;
    letter-spacing: -0.025em;
    text-align: center;
    @media screen and (max-width: 1300px) {
      font-size: 50px;
    }
    @media screen and (max-width: 992px) {
      font-size: 40px;
    }
  }
}
.title {
  @include worksansbold;
  font-size: 42px;
  @media screen and (max-width: 1300px) {
    font-size: 36px;
  }
  @media screen and (max-width: 992px) {
    font-size: 30px;
  }
}
