.main-header {
  height: 1124px;
  background-repeat: no-repeat;
  background-position: bottom center;
  @media screen and (max-width: 900px) {
    border-bottom: 4px solid $dodger-blue;
  }
  @media screen and (max-width: 600px) {
    background-position: 67% 100%;
  }
  

  &__top {
    background: $fun-blue;


    &-wrap {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 auto;
      padding: 80px 15px 0;
      max-width: 1840px;
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 60px;
    padding: 16px;
    color: white;
    text-decoration: none;
    transition: all .5s;
    @media screen and (max-width: 1360px) {
      margin-right: 20px;
    }
    @media screen and (max-width: 1260px) {
      margin-right: 0;
    }


    &:hover {
      color: $dodger-blue;
      background: $bahama-blue;
    }

    svg {
      margin-right: 10px;
    }
  }
}

.main-menu {
  display: flex;
  align-items: center;
  margin-right: 250px;
  @media screen and (max-width: 1560px) {
    margin-right: 150px;
  }
  @media screen and (max-width: 1460px) {
    margin-right: 50px;
  }
  @media screen and (max-width: 1260px) {
    display: block;
    text-align: center;
  }

  a {
    display: block;
    padding: 16px;
    color: white;
    text-decoration: none;
    transition: all .5s;

    &:hover {
      color: $dodger-blue;
      background: $bahama-blue;
    }
  }

  &__wrap {
    display: flex;
    align-items: center;
    @media screen and (max-width: 1260px) {
      display: none;
    }
    &.active {
      position: fixed;
      top: 0;
      left: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      color: white;
      background: $fun-blue;
      z-index: 1000;
  }
  }

  &__buttons {
    display: flex;
    align-items: center;
    @media screen and (max-width: 1260px) {
      width: 100%;
      flex-direction: column;
      text-align: center;
      border-bottom: 1px solid $dodger-blue;
    }
    li {
      @media screen and (max-width: 1260px) {
        position: relative;
        left: -20px;
      }

      &:nth-child(2) {
        @media screen and (max-width: 1260px) {
          left: 0;
          order: 2;
        }
      }

    }
  }
}

.languages {
  margin-right: 80px;
  @media screen and (max-width: 1360px) {
    margin-right: 30px;
  }
  @media screen and (max-width: 1260px) {
    margin-right: 0;
  }


  li {
    display: none;

    &:first-child {
      display: block;
    }

    a {
      display: block;
      padding: 16px;
      color: white;
      text-decoration: none;
    }
  }
}

.main-header__center {
  position: relative;
  top: 218px;
  @media screen and (max-width: 576px) {
  	top: 30px;
  }
}

.main-header__subtitle {
  margin-bottom: 20px;
  font-family: "worksansbold";
  font-size: 50px;
  font-weight: bold;
  line-height: 1.1;
  letter-spacing: -1.275px;
  color: white;
  @media screen and (max-width: 700px) {
    font-size: 40px;
  }
}

.main-header__title {
  margin-bottom: 20px;
  @include worksansregular;
  font-size: 40px;
  font-weight: 300;
  letter-spacing: -0.025em;
  color: white;
  @media screen and (max-width: 700px) {
    font-size: 28px;
  }
}

.download-button {
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  height: 65px;
  color: white;

  &:hover {
    span {
      background: $dodger-blue;
    }
  }

  span {
    display: flex;
    align-items: center;
    padding: 0 20px;
    width: 232px;
    height: 100%;
    letter-spacing: -0.3px;
    background: $fun-blue;
    transition: all .5s;
  }

  &__image {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 8px;
    height: 100%;
    background: $dodger-blue;
  }
}

.btn-emergency {
  position: absolute;
  top: 0;
  right: 16%;
  width: 203px;
  height: 47px;
  font-family: Graphik, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 47px;
  text-align: center;
  text-decoration: none;
  color: white;
  background: #C90000;
  transition: all .3s;

  &:hover {
    color: #C90000;
    background: white;
  }
}

.show-menu {
  position: absolute;
  top: 80px;
  right: 30px;
  display: none;
  align-items: center;
  @media screen and (max-width: 1260px) {
    display: flex;
  }

}
