.success {
  padding-top: 60px;

  &__title {
    margin-bottom: 90px;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    padding: 0 30px;
    @media screen and (max-width: 1400px) {
      flex-wrap: wrap;
    }
    @media screen and (max-width: 768px) {
      display: block;
    }
  }

  &__item {
    position: relative;
    margin-right: 115px;
    @media screen and (max-width: 1400px) {
      margin-bottom: 60px;
      width: 38%;
      text-align: center;
    }
    @media screen and (max-width: 768px) {
      margin: 0 auto 60px;
      max-width: 500px;
      width: auto;
    }

    &:last-child {
      margin-right: 0;
      @media screen and (max-width: 768px) {
        margin-right: auto;
      }
    }

    &:nth-child(2n) {
      @media screen and (max-width: 1400px) {
        margin-right: 0;
      }
      @media screen and (max-width: 768px) {
        margin-right: auto;
      }
    }
  }

  &__img-wrap {
    position: relative;
    margin-bottom: 45px;
    width: 340px;
    height: 210px;
    @media screen and (max-width: 1800px) {
      width: 300px;
    }
    @media screen and (max-width: 1600px) {
      width: 240px;
    }
    @media screen and (max-width: 1400px) {
      margin: 0 auto 45px;
      width: 340px;
    }
    @media screen and (max-width: 576px) {
      width: auto;
    }
  }
  &__img {
    overflow: hidden;

    img {
      transition: all .4s;
    }

    &:hover {
      img {
        transform: scale(1.1);
      }
    }
  }

  &__icon {
    position: absolute;
    top: -40px;
    left: -40px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90px;
    height: 90px;
    font-size: 50px;
    font-weight: bold;
    border-radius: 50%;
    color: white;
    background: $dodger-blue;
    z-index: 10;
    transition: all .3s;
    @media screen and (max-width: 576px) {
      left: 8%;
      width: 30px;
      height: 30px;
      font-size: 20px;
    }
    @media screen and (max-width: 460px) {
    	left: 0;
    }

    &:hover {
      transform: scale(1.1);
    }
  }
}

.success__item-title {
  margin-bottom: 24px;
  text-align: center;
}

.success__text {
  @include franklinkgothicdemi;
  font-size: 24px;
  color: $fun-blue;
  letter-spacing: -0.01em;
  text-align: center;
}