.features {
  &__wrap {
    display: flex;
    justify-content: space-between;
    padding: 60px 0 0;
    @media screen and (max-width: 1320px) {
      flex-wrap: wrap;
      justify-content: center;
    }
    @media screen and (max-width: 640px) {
      display: block;
    }
  }

  &__item {
    flex: 1;
    margin-right: 60px;
    text-align: center;
    transition: all .2s;
    transform: translateY(0);
    @media screen and (max-width: 1320px) {
      flex: 0 0 240px;
      margin-bottom: 40px;
    }
    @media screen and (max-width: 640px) {
      margin-right: 0;
    }

    &:hover {
      @media screen and (min-width: 1320px) {
        transform: translateY(-50px);
      }
      cursor: pointer;

      .features__text {
        transform: translateY(0);
        opacity: 1;
      }
    }

    &:last-child {
      margin-right: 0;
    }
  }

  svg {
    margin: 0 auto 30px;
    width: 160px;
  }

  &__title {
    color: $fun-blue;
    @include worksansbold;
    letter-spacing: -0.025em;
    text-transform: capitalize;
    font-size: 42px;
    @media screen and (max-width: 1800px) {
      font-size: 30px;
    }
  }

  &__text {
    @include franklinkgothicdemi;
    font-size: 24px;
    font-weight: bold;
    color: $fun-blue;
    line-height: 32px;
    transition: all .3s;
    transform: translateY(100px);
    opacity: 0;
    @media screen and (max-width: 1320px) {
      font-size: 20px;
      line-height: 1.2;
      transform: translateY(0);
      opacity: 1;
    }
  }
}