.sandwitch-wrap
{
  display: block;
  padding: 18px 10px;
  border-radius: 3px;
  z-index: 1001;
  @media screen and (max-width: 992px)
  {
    top: 30px;
    right: 30px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
  }
}

.sandwitch
{
  position: relative;
  display: block;
  width: 23px;
  height: 14px;
  cursor: pointer;
}

.sandwitch--active .sandwitch__line--top
{
  top: 6px;
  transform: rotate(45deg);
  width: 100%;
}

.sandwitch--active .sandwitch__line--middle
{
  opacity: 0;
}

.sandwitch--active .sandwitch__line--bottom
{
  top: 6px;
  transform: rotate(-45deg);
  width: 100%;
}

.sandwitch__line
{
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  height: 2px;
  transition: all linear 0.1s;
  background-color: $dodger-blue;
}

.sandwitch__line--top
{
  top: 0;
  width: 80%;
}

.sandwitch__line--middle
{
  top: 6px;
}

.sandwitch__line--bottom
{
  top: 12px;
  width: 80%;
}
