.solutions {
  padding-top: 70px;
  color: $fun-blue;
  background: $anakiwa;
  @media screen and (max-width: 1300px) {
    padding-bottom: 60px;
  }


  &__wrap {
    display: flex;
    flex-wrap: wrap;
    @media screen and (max-width: 1300px) {
      display: block;
      margin: 0 auto;
      max-width: 900px;
    }
  }

  &__item {
    margin-right: 58px;
    margin-bottom: 60px;
    width: 47%;
    @media screen and (max-width: 1300px) {
      margin-right: 0;
      width: auto;
    }

    &:nth-child(2n) {
      margin-right: 0;
    }

    &-title {
      margin-bottom: 10px;
    }

    p {
      margin-bottom: 30px;
    }
  }

  &__image {
    margin-bottom: 50px;
    height: 400px;
    overflow: hidden;
    @media screen and (max-width: 1800px) {
      height: 293px;
    }
    @media screen and (max-width: 1300px) {
      height: auto;
    }

    &:hover {
      img {
        transform: scale(1.2);
      }
    }

    img {
      transition: all .6s;
    }
  }
}