.we-can {
  padding: 90px 15px 80px;
  color: white;
  background: $fun-blue;
  &__title {
    @include worksansbold;
    font-weight: bold;
    font-size: 72px;
    letter-spacing: -0.025em;
    text-align: center;
    color: #fff;
    padding-bottom: 60px;
    @media screen and (max-width: 992px) {
      font-size: 50px;
    }
    @media screen and (max-width: 576px) {
      font-size: 40px;
    }
  }
  &__wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 1360px) {
      display: block;
    }

  }
  &__image {
    @media screen and (max-width: 1360px) {
      display: block;
      margin: 40px auto 40px;
    }
  }
  &__item {
    position: relative;
    padding: 30px;
    font-size: 30px;
    line-height: 1.5;
    text-align: justify;
    border-radius: 10px;
    box-shadow: 4px 4px 16px rgba(0,0,0,.1);
    @media screen and (max-width: 576px) {
      font-size: 24px;
      text-align: left;
    }

    &::after {
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 0 30px 15px 30px;
      border-color: transparent transparent $dodger-blue transparent;
    }
    &.we-can__item--colored {
      flex: 0 0 590px;
      margin-right: 100px;
      padding: 50px;
      color: white;
      background: $dodger-blue;
      @media screen and (max-width: 1580px) {
        margin-right: 30px;
      }
      @media screen and (max-width: 1500px) {
      	flex: 0 0 460px;
      }
      @media screen and (max-width: 576px) {
        margin-right: 0;
      }
      &::after {
        bottom: -14px;
        right: 28px;
        transform: rotate(30deg);
        @media screen and (max-width: 1360px) {
          transform: rotate(330deg);
        }
      }
    }
  }
}
.we-can__block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex: 1;
  @media screen and (max-width: 1360px) {
    flex-direction: row;
  }
  @media screen and (max-width: 992px) {
    flex-direction: column;
    justify-content: center;
  }
  @media screen and (max-width: 490px) {
    display: block;
  }

  .we-can__item {
    color: $fun-blue;
    background: white;
    &::after {
      border-width: 0 30px 20px 30px;
      border-color: transparent transparent white transparent;
    }
    &:first-child {
      margin-bottom: 30px;
      width: 404px;
      @media screen and (max-width: 1360px) {
        margin-right: 40px;
      }
      @media screen and (max-width: 490px) {
        margin-right: 0;
        width: auto;
      }
      &::after {
        top: 134px;
        left: -30px;
        transform: rotate(45deg);
        @media screen and (max-width: 1360px) {
          top: -17px;
          left: initial;
          right: 60px;
          transform: rotate(146deg);
        }

      }
    }
    &:last-child {
      position: relative;
      &::after {
        bottom: -17px;
        left: 33px;
        transform: rotate(-32deg);
      }
      &::before {
        content: '';
        position: absolute;
        top: -20px;
        right: -20px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        border: 8px solid $fun-blue;
        background: url("../i/we-can-icon-cropped.png") center;
        background-size: cover;
        overflow: hidden;
      }
    }
  }
}